*{
  box-sizing:border-box
}

body{
  font-family:ui-sans-serif, system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji"
}

h1,h2,h3,h4,h5,h6{
  margin:0;
  padding:0
}

img{
  width:100%;
  height:100%
}

.default-input{
  border:none;
  border-bottom:1px solid #d76b3c;
  outline:none;
  background-color:inherit
}

@media screen and (min-width: 1100px){
  .main-layout{
    max-width:1300px;
    margin:0 auto
  }
}

*:hover>.show-on-hover{
  display:inline;
  display:initial
}

.show-on-hover{
  display:none
}

.app-header-container{
  background-color:#f1f1f1;
  position:fixed;
  width:100%
}

.app-header-container .logo{
  font-size:2.5em;
  text-transform:uppercase
}

.app-header-container .nav-link{
  text-decoration:none;
  font-size:1em;
  text-transform:uppercase;
  color:#737373
}

.app-header-container .nav-link:not(:last-child){
  -webkit-margin-end:20px;
  margin-inline-end:20px
}

.app-header-container+*{
  padding-top:100px
}

.excel-list{
  grid-column-gap:20px
}

.excel-preview{
  border:1px solid #d76b3c;
  min-height:200px;
  min-width:160px;
  color:#d76b3c;
  position:relative
}

.excel-preview .title{
  font-weight:500;
  transition:background-color 0.22s
}

.excel-preview:hover .title{
  color:#fff;
  background-color:#d76b3c
}

.excel-preview .btn-actions{
  display:none
}

.excel-preview:hover .btn-actions{
  display:inline
}

.excel-add{
  transition:background-color 0.25s
}

.excel-add .upload-label{
  width:100%;
  height:100%
}

.excel-add:hover{
  background-color:#d76b3c;
  color:#fff
}

.sheet-preview{
  transition:background-color 0.12s
}

.sheet-preview:hover{
  color:#fff;
  background-color:#d76b3c
}

.data-page .search-results .filtered-cells-list{
  margin:0;
  max-height:400px;
  background-color:#f1f1f1;
  padding:10px;
  overflow-y:auto
}

.data-page .search-results .filtered-cells-list-item{
  transition:background-color 0.1s;
  width:100%
}

.data-page .search-results .filtered-cells-list-item:hover{
  color:#fff;
  background-color:#d76b3c
}

.data-page .search-results .filtered-cells-list-item .text{
  flex-basis:90%
}

.data-page .search-results .filtered-cells-list-item .btn-action{
  visibility:hidden
}

.data-page .search-results .filtered-cells-list-item:hover .btn-action{
  visibility:visible
}

.data-grid-layout{
  width:80%
}

.data-filter input{
  outline:none;
  border:none;
  padding:10px;
  width:200px;
  border-bottom:1px solid #d76b3c;
  transition:width 0.4s
}

.data-filter input:focus{
  background-color:#f1f1f1;
  width:100%
}

.data-filter .col-filter{
  width:300px;
  height:100%;
  border:none;
  outline:none;
  border-bottom:1px solid #d76b3c;
  cursor:pointer
}

.data-filter .col-filter:focus{
  background-color:#f1f1f1
}

.default-actions-popover-container .default-actions-popover{
  cursor:auto;
  cursor:initial;
  background-color:#fff;
  position:absolute;
  z-index:10;
  width:250px;
  height:300px;
  border-radius:5px;
  box-shadow:0px 0px 4px #494949;
  overflow-x:hidden
}

.default-actions-popover-container .default-actions-popover-header{
  text-align:center;
  font-family:"Open Sans Bold";
  border-bottom:1px solid #e0e0e0;
  padding-bottom:5px;
  padding-top:5px;
  margin-bottom:5px;
  font-size:0.8em
}

.default-actions-popover-container .default-actions-popover-list{
  list-style:none;
  padding-left:0;
  text-align:start;
  margin-top:10px
}

.default-actions-popover-container .default-actions-popover-list>*{
  padding:10px 8px;
  cursor:pointer
}

.default-actions-popover-container .default-actions-popover-list>*:hover{
  color:#404040;
  background-color:#fbeacd
}

.default-actions-popover-container .default-actions-popover-list>*::after{
  content:"..."
}

.default-actions-popover-container .btn-actions{
  position:absolute;
  top:10px;
  right:5px;
  font-size:1.6rem;
  padding:0.25rem;
  border-radius:50px
}

.default-actions-popover-container .btn-actions:hover{
  background-color:#f1f1f1
}

.btn-action{
  border-radius:50px;
  cursor:pointer;
  padding:0.25rem
}

.btn-action:hover{
  background-color:#f1f1f1;
  color:#212121
}

.data-corr .data-corr-modal{
  background-color:grey;
  color:#212121;
  border-radius:20px 20px 0 0;
  height:calc(100vh - 100px);
  padding:20px;
  position:absolute;
  bottom:-100%;
  left:10%;
  right:10%;
  transition:bottom 0.3s
}

.data-corr .data-corr-modal.open{
  bottom:0
}

